"use client";

import { useEffect, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import Label from "@/components/form/Label";
import { CompanyService } from "@/services/company.service";
import { ProductService } from "@/services/product.service";
import toast from "react-hot-toast";

/* =========================
   Types
========================= */
interface Branch {
  id: string;
  branchName: string;
}

interface Product {
  id: string;
  name: string;
}

export interface StockConvertUnitFormData {
  fromProductId: string;
  toProductId: string;
  branchId: string;
  openQuantity: number;
  conversionValue: number;
}

const emptyForm: StockConvertUnitFormData = {
  fromProductId: "",
  toProductId: "",
  branchId: "",
  openQuantity: 0,
  conversionValue: 0,
};

export default function StockConvertUnitFormModal({
  open,
  onClose,
  onSubmit,
}: {
  open: boolean;
  onClose: () => void;
  onSubmit: (data: StockConvertUnitFormData) => void;
}) {
  const [form, setForm] = useState<StockConvertUnitFormData>(emptyForm);
  const [branches, setBranches] = useState<Branch[]>([]);
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(false);

  /* =========================
     LOAD DATA
  ========================= */
  useEffect(() => {
    if (!open) return;

    Promise.all([
      CompanyService.getAllBranches(),
      ProductService.getproduct(),
    ])
      .then(([b, p]) => {
        setBranches(b.data.data);
        setProducts(p.data.data);
      })
      .catch(() => toast.error("Failed to load data"));
  }, [open]);

  /* =========================
     UPDATE FIELD
  ========================= */
  const update = <K extends keyof StockConvertUnitFormData>(
    key: K,
    value: StockConvertUnitFormData[K]
  ) => {
    setForm((prev) => ({ ...prev, [key]: value }));
  };

  /* =========================
     VALIDATION
  ========================= */
  const validate = () => {
    if (!form.branchId) return toast.error("Branch is required"), false;
    if (!form.fromProductId) return toast.error("From Product is required"), false;
    if (!form.toProductId) return toast.error("To Product is required"), false;

    if (form.fromProductId === form.toProductId)
      return toast.error("Products must be different"), false;

    if (form.openQuantity <= 0)
      return toast.error("Open quantity must be greater than zero"), false;

    if (form.conversionValue <= 0)
      return toast.error("Conversion value must be greater than zero"), false;

    return true;
  };

  /* =========================
     SUBMIT
  ========================= */
  const submit = async () => {
    if (!validate()) return;
    setLoading(true);
    await onSubmit(form);
    setLoading(false);
    setForm(emptyForm);
  };

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
      <div className="w-full max-w-3xl rounded-xl bg-white dark:bg-gray-900 shadow-xl">
        {/* Header */}
        <div className="relative p-5 border-b dark:border-gray-800">
          <h3 className="text-lg font-semibold text-center">
            Convert Product Unit
          </h3>
          <button
            onClick={onClose}
            className="absolute right-5 top-1/2 -translate-y-1/2 text-2xl"
          >
            Ã—
          </button>
        </div>

        {/* Body */}
        <div className="p-6 grid grid-cols-1 md:grid-cols-2 gap-6">
          <Field label="Branch" required>
            <select
              className="w-full rounded-md border px-3 py-2"
              value={form.branchId}
              onChange={(e) => update("branchId", e.target.value)}
            >
              <option value="">Select branch</option>
              {branches.map((b) => (
                <option key={b.id} value={b.id}>
                  {b.branchName}
                </option>
              ))}
            </select>
          </Field>

          <Field label="From Product" required>
            <select
              className="w-full rounded-md border px-3 py-2"
              value={form.fromProductId}
              onChange={(e) => update("fromProductId", e.target.value)}
            >
              <option value="">Select source product</option>
              {products.map((p) => (
                <option key={p.id} value={p.id}>
                  {p.name}
                </option>
              ))}
            </select>
          </Field>

          <Field label="To Product" required>
            <select
              className="w-full rounded-md border px-3 py-2"
              value={form.toProductId}
              onChange={(e) => update("toProductId", e.target.value)}
            >
              <option value="">Select target product</option>
              {products.map((p) => (
                <option key={p.id} value={p.id}>
                  {p.name}
                </option>
              ))}
            </select>
          </Field>

          <Field label="Open Quantity" required>
            <Input
              type="number"
              value={form.openQuantity}
              onChange={(e) =>
                update("openQuantity", Number(e.target.value))
              }
            />
          </Field>

          <Field label="Conversion Value" required>
            <Input
              type="number"
              value={form.conversionValue}
              onChange={(e) =>
                update("conversionValue", Number(e.target.value))
              }
            />
          </Field>
        </div>

        {/* Footer */}
        <div className="p-5 border-t flex justify-end gap-3">
          <Button variant="outline" onClick={onClose}>
            Cancel
          </Button>
          <Button onClick={submit} disabled={loading}>
            {loading ? "Converting..." : "Convert"}
          </Button>
        </div>
      </div>
    </div>
  );
}

/* =========================
   Field Helper
========================= */
function Field({
  label,
  children,
  required,
  full,
}: {
  label: string;
  children: React.ReactNode;
  required?: boolean;
  full?: boolean;
}) {
  return (
    <div className={full ? "md:col-span-2" : ""}>
      <Label>
        {label} {required && <span className="text-red-500">*</span>}
      </Label>
      {children}
    </div>
  );
}
