"use client";

import { useEffect, useRef, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import StockConvertUnitFormModal, {
  StockConvertUnitFormData,
} from "./StockConvertFormModal";
import { ProductService } from "@/services/product.service";
import toast from "react-hot-toast";

/* =========================
   Types
========================= */
interface StockDto {
  productId: string;
  productName: string;
  branchName: string;
  quantity: number;
  isLowStock: boolean;
  unit?: string;
}

/* =========================
   Action Menu
========================= */
function ActionMenu({ onConvert }: { onConvert: () => void }) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const close = (e: MouseEvent) => {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        setOpen(false);
      }
    };
    document.addEventListener("mousedown", close);
    return () => document.removeEventListener("mousedown", close);
  }, []);

  return (
    <div ref={ref} className="relative">
      <button onClick={() => setOpen((p) => !p)}>â‹®</button>

      {open && (
        <div className="absolute right-0 z-50 w-36 rounded border bg-white shadow">
          <button
            onClick={() => {
              onConvert();
              setOpen(false);
            }}
            className="w-full px-4 py-2 text-left hover:bg-gray-100"
          >
            ðŸ”„ Convert
          </button>
        </div>
      )}
    </div>
  );
}

/* =========================
   Convert Unit Table
========================= */
export default function StockConvertUnitTable() {
  const [items, setItems] = useState<StockDto[]>([]);
  const [search, setSearch] = useState("");
  const [loading, setLoading] = useState(false);
  const [openConvert, setOpenConvert] = useState(false);

  /* =========================
     LOAD DATA
  ========================= */
  async function loadStocks() {
    try {
      setLoading(true);
      const res = await ProductService.getAllStocks();
      setItems(res.data.data);
    } catch {
      toast.error("Failed to load stocks");
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    loadStocks();
  }, []);

  /* =========================
     CONVERT
  ========================= */
  async function handleConvert(data: StockConvertUnitFormData) {
    try {
      await ProductService.CreateStockConvertAsyn(data);
      toast.success("Product unit converted successfully");
      setOpenConvert(false);
      loadStocks();
    } catch {
      toast.error("Conversion failed");
    }
  }

  /* =========================
     FILTER
  ========================= */
  const filteredItems = items.filter(
    (x) =>
      x.productName.toLowerCase().includes(search.toLowerCase()) ||
      x.branchName.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="bg-white p-6 rounded-xl">
      {/* Header */}
      <div className="flex justify-between mb-4">
        <Input
          placeholder="Search product or branch..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-64"
        />
        <Button onClick={() => setOpenConvert(true)}>
          + Open Products
        </Button>
      </div>

      {/* Table */}
      <table className="w-full border-collapse">
        <thead className="bg-gray-50">
          <tr>
            <th className="px-3 py-2 text-left">Product</th>
            <th className="px-3 py-2 text-left">Branch</th>
            <th className="px-3 py-2 text-left">Quantity</th>
            <th className="px-3 py-2 text-left">Status</th>
            <th className="px-3 py-2 text-left">Unit</th>
            <th className="px-3 py-2 text-right">Action</th>
          </tr>
        </thead>

        <tbody>
          {loading ? (
            <tr key="loading">
              <td colSpan={6} className="py-6 text-center">
                Loading...
              </td>
            </tr>
          ) : filteredItems.length === 0 ? (
            <tr key="empty">
              <td colSpan={6} className="py-6 text-center text-gray-500">
                No products found
              </td>
            </tr>
          ) : (
            filteredItems.map((x) => (
              <tr
                key={`${x.productId}-${x.branchName}`} // âœ… SAFE KEY
                className="border-t"
              >
                <td className="px-3 text-sm text-gray-600 dark:text-gray-400">{x.productName}</td>
                <td className="x-3 text-sm text-gray-600 dark:text-gray-400">{x.branchName}</td>
                <td className="x-3 text-sm text-gray-600 dark:text-gray-400">{x.quantity}</td>
                <td className="px-3 py-2">
                  {x.isLowStock ? (
                    <span className="text-red-600 x-3 text-sm ">Low</span>
                  ) : (
                    <span className="text-green-600 text-sm ">Normal</span>
                  )}
                </td>
                <td className="px-3 text-sm text-gray-600 dark:text-gray-400">{x.unit ?? "-"}</td>
                <td className="px-3 py-2 text-right">
                  <ActionMenu onConvert={() => setOpenConvert(true)} />
                </td>
              </tr>
            ))
          )}
        </tbody>
      </table>

      {/* CONVERT MODAL */}
      <StockConvertUnitFormModal
        open={openConvert}
        onClose={() => setOpenConvert(false)}
        onSubmit={handleConvert}
      />
    </div>
  );
}
